<template>
	<div class="notes">
		<div class="topiclist">
			<div
				class="item"
				@click="goTopiclistDetail(item.id)"
				v-for="(item, index) in topiclist"
				:key="index"
			>
				#{{ item.name }}
			</div>
		</div>

		<waterfall
			:col="col"
			:width="itemWidth"
			:gutterWidth="gutterWidth"
			:data="data"
			@loadmore="loadmore"
		>
			<template>
				<div
					class="cell-item"
					@click="goNotesDetail(item.id)"
					v-for="(item, index) in data"
					:key="index"
				>
					<img class="auto-img bdrimg" :src="item.image_u" alt="加载错误" />
					<div class="punch" v-if="item.straight_text">
						<div class="punch-img">
							<img class="auto-img" src="../assets/icon/daka.png" alt="" />
						</div>
						<div class="punch-text">{{ item.straight_text }}</div>
					</div>
					<div class="item-body">
						<div class="item-desc">{{ item.title }}</div>
					</div>

					<div class="head">
						<div class="user" @click.stop="goUserDetail(item.author.id)">
							<div class="head-img">
								<img class="auto-img" :src="item.author.p" alt="" />
							</div>
							<div class="username">{{ item.author.n }}</div>
						</div>
						<div class="like_count">
							<div class="like-img">
								<img
									ref="itemimg"
									class="auto-img img-like"
									@click.stop="goLogin(index, item)"
									src="../assets/icon/love.png"
									alt=""
								/>
							</div>
							<div class="like-sum">
								{{ item.like_count }}
							</div>
						</div>
					</div>
				</div>
			</template>
		</waterfall>
	</div>
</template>
<script>
import { Toast } from "vant";
export default {
	name: "Notes",
	data() {
		return {
			isImg: false,
			isLike: false,
			data: [],
			rfsData: [],
			offset: 0,
			limit: 20,
			col: 2,
			page: 1,
			topiclist: [],
		};
	},
	computed: {
		itemWidth() {
			return 350 * 0.5 * (document.documentElement.clientWidth / 375); // #rem布局 计算宽度
		},
		gutterWidth() {
			return 9 * 0.5 * (document.documentElement.clientWidth / 375); //#rem布局 计算x轴方向margin(y轴方向的margin自定义在css中即可)
		},
	},

	created() {
		this.getTake();
		this.getTopiclist();
	},

	methods: {

		loadmore() {
			this.offset += this.limit + 10;
			this.getTake();
		},

		getTake() {
			this.axios({
				method: "get",
				url:
					"https://apis.netstart.cn/douguo/home/notes/" +
					this.offset +
					"/" +
					this.limit,
			})
				.then((result) => {
					this.data = [];
					for (let i = 0; i < result.data.result.list.length; i++) {
						if (result.data.result.list[i].type == 2) {
							continue;
						} else {
							this.rfsData.push(result.data.result.list[i]);
						}
					}
					for (let z = 0; z < this.rfsData.length; z++) {
						if (this.rfsData[z].note == undefined) {
							continue;
						} else {
							this.data.push(this.rfsData[z].note);
						}
					}
					
				})
				.catch((err) => {
					
				});
		},

		goNotesDetail(id) {
			this.$router.push({ name: "Notesdetail", params: { id } });
		},

		getTopiclist() {
			this.axios({
				method: "get",
				url: "https://apis.netstart.cn/douguo/note/topiclist/0/20",
			})
				.then((result) => {
					
					this.topiclist = result.data.result.list;
					
				})
				.catch((err) => {
					
				});
		},

		goTopiclistDetail(id) {
			this.$router.push({ name: "Topiclistdetail", params: { id } });
		},

		goLogin(index, item) {
			if (!this.$root.isLogin) {
				this.$router.push({ name: "Login" });
			} else {
				if (!this.isImg) {
					this.$refs.itemimg[
						index
					].src = require("../assets/icon/love-active.png");
					item.like_count = item.like_count + 1;
					Toast("点赞成功");
					this.isImg = true;
				} else {
					
					this.$refs.itemimg[index].src = require("../assets/icon/love.png");
					item.like_count = item.like_count - 1;
					Toast("取消点赞");
					this.isImg = false;
				}
			}
		},

		goUserDetail(id) {
			this.$router.push({ name: "Userdetail", params: { id } });
		},
	},
};
</script>

<style lang="less" scoped>
.bdrimg{
	border-radius: 5px;
}

.head {
	display: flex;
	align-items: center;
	margin-bottom: 10px;
	.user {
		display: flex;
		align-items: center;
		padding-left: 5px;
		.username {
			padding-left: 5px;
			color: #888;
			text-overflow: ellipsis;
			display: -webkit-box;
			-webkit-line-clamp: 1;
			-webkit-box-orient: vertical;
			overflow: hidden;
		}
		.head-img {
			width: 15px;
			height: 15px;
			img {
				border-radius: 50%;
			}
		}
	}

	.like_count {
		display: flex;
		align-items: center;
		margin-left: auto;
		.like-img {
			width: 25px;
		}
	}
}
.item-desc {
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 2;
	-webkit-box-orient: vertical;
	overflow: hidden;
}
.punch {
	display: flex;
	align-items: center;
	padding: 5px 5px 0 5px;
	.punch-img {
		width: 15px;
		height: 15px;
		margin-right: 5px;
	}
	.punch-text {
		font-size: 12px;
		color: #888;
	}
}

.item-body {
	padding: 5px;
}

.notes {
	padding: 0 10px;
	margin-top: 118px;
	margin-bottom: 50px;
	.topiclist {
		display: flex;
		flex-wrap: wrap;
		.item {
			margin: 0 5px;
			color: #5e8f99;
			font-weight: bold;
			margin-bottom: 10px;
			font-size: 12px;
			padding: 8px 15px;
			border-radius: 20px;
			background-color: #f3f8fa;
		}
	}
}
</style>
